form {
    font-family: 'Courier New', Courier, monospace;
    display: flex;
    flex-direction: column;
    max-width: 400px;
    margin: 0 auto;
  }
  
  label {
    margin-top: 10px;
    font-size: 16px;
    font-weight: bold;
  }
  
  input[type="text"],
  input[type="email"],
  textarea {
    padding: 10px;
    font-size: 16px;
    border: none;
    border-radius: 5px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    margin-bottom: 20px;
  }
  
  input[type="submit"] {
    background-color: #007bff;
    color: #fff;
    border: 1px solid #007bff;
    border-radius: 5px;
    padding: 10px 20px;
    font-size: 16px;
    cursor: pointer;
    transition: background-color 0.2s ease-in-out;
  }
  
  input[type="submit"]:hover {
    background-color: #eff4f9;
    color: black;
  }
  textarea {
    height: 100px;
    max-width: 400px;
  }
  h3 {
    text-align: center;
    font-size: large;
    font-weight: bolder;
    font-family: 'Courier New', Courier, monospace;
    margin-bottom: 0.8rem;
  }
  